export function loadGoogleMapsScript(apiKey: string): Promise<void> {
  return new Promise((resolve, reject) => {
    const script = document.createElement('script')
    script.src = `https://maps.googleapis.com/maps/api/js?key=${apiKey}&libraries=places`
    script.async = true
    script.defer = true
    script.onload = () => resolve()
    script.onerror = () =>
      reject(new Error('Failed to load Google Maps script'))
    document.head.appendChild(script)
  })
}

export function initMap(): google.maps.Map {
  const mapElement = document.getElementById('map') as HTMLElement
  return new google.maps.Map(mapElement, {
    center: { lat: 0, lng: 0 },
    zoom: 2,
  })
}

export function searchLocation(map: google.maps.Map, query: string) {
  const geocoder = new google.maps.Geocoder()
  geocoder.geocode({ address: query }, (results, status) => {
    if (status === 'OK' && results[0]) {
      map.setCenter(results[0].geometry.location)
      map.setZoom(10)
      addMarkers(map, results[0].geometry.location)
    } else {
      alert('Location not found')
    }
  })
}

export function addMarkers(map: google.maps.Map, center: google.maps.LatLng) {
  for (let i = 0; i < 1000; i++) {
    const position = new google.maps.LatLng(
      center.lat() + (Math.random() - 0.5) * 0.1,
      center.lng() + (Math.random() - 0.5) * 0.1,
    )
    new google.maps.Marker({ position, map })
  }
}
